import { createApp } from 'vue';
import App from './App.vue';
import store from './store';
import router from './router';
import '@fortawesome/fontawesome-free/css/all.min.css';
import axios from 'axios';
import PrimeVue from 'primevue/config';
import Calendar from 'primevue/calendar';
import 'primevue/resources/themes/saga-blue/theme.css'; // Theme
import 'primevue/resources/primevue.min.css';           // Core CSS
import 'primeicons/primeicons.css';                     // Icons
import 'primeflex/primeflex.css';                       // PrimeFl

import { registerLicense } from '@syncfusion/ej2-base'

registerLicense("Ngo9BigBOggjHTQxAR8/V1NCaF5cXmZCdkx3Q3xbf1x0ZFRMZV5bQHBPIiBoS35RckVkWHZfcnFRQ2BaVUd0")

// Configurar Axios para incluir el token en todas las solicitudes
axios.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// Crear la aplicacin Vue
const app = createApp(App);

// Usar las bibliotecas necesarias
app.component('Calendar', Calendar);
app.use(PrimeVue);
app.use(store);
app.use(router);

// Configurar la aplicacin para no mostrar el tip de produccin
app.config.productionTip = false;

// Montar la aplicacin
app.mount('#app');